import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faDownload, faEye} from "@fortawesome/free-solid-svg-icons";
import {getStaticURL, socialLinks} from "../../utils";
import {useEffect, useState} from "react";

const TypeWriterIntro = () => {
	const positions = [
		'A Student',
		'A Physicist',
		'A Tech Enthusiast',
		'A fond of Quantum Physics',
	];
	const [idx, setIdx] = useState(0);
	const [inc, setInc] = useState(true);
	useEffect(() => {
		if (inc) {
			setTimeout(() => {
				if (idx < positions.length - 1) setIdx(idx + 1); else setIdx(0);
				setInc(false)
			}, 2200)
		} else {
			setTimeout(() => {
				setInc(true)
			}, 50)
		}

	}, [inc])
	return (
		<>
			<h3 className="d-flex align-items-center mb-5">
				I'm&nbsp;
				<div
					className={`text-secondary overflow-hidden d-inline-block${inc ? " typewriter" : " d-none"}`}
				>{positions[idx]}</div>
			</h3>
		</>
	)
}

